@import '../../../common/variables';
.social {
	margin-top: 40px;

	.socialList {
		& li {
			display: inline-block;
			padding: 3px;
			overflow: hidden;

			& a {
				border-radius: 50%;
				border: 1px solid #FFFFFF;
				color: $text-color1;
				opacity: .6;
				background: transparent;
				display: block;
				font-size: 20px;
				height: 36px;
				width: 36px;
				line-height: 34px;
				margin: 0 0.1em 0.2em 0;
				padding: 0;
				text-align: center;

				&:hover, &:focus, &:active {
					-webkit-tap-highlight-color: transparent;
					text-decoration: none;
					outline: none;
					background-color: $main-color;
					opacity: 1;
					border: none; } } } } }
