@import '../../common/variables';

.header {
	position: relative;
	display: flex;
	height: 900px;
	background-attachment: fixed !important;
	background-position: center bottom;
	background-size: cover;
	@media screen and (max-width: 400px) {
		background-attachment: scroll !important;
		background-position: center;
		background-size: cover; }

	&::before {
		position: absolute;
		content: '';
		background-color: rgba(53, 60, 79, .8);
		top: 0;
		bottom: 0;
		left: 0;
		right: 0; }

	@media screen and (max-width: 400px) {
		min-height: 930px; }
	@media screen and (max-width: 375px) {
		min-height: 900px; }

	.contentContainer {
		padding: 50px 20px;
		text-align: center;
		position: relative;
		z-index: 1;
		@media screen and (max-width: 300px) {
			padding: 50px 0; }

		.content {
			padding: 20px 0;

			.titles {
				& h2 {
					color: $text-color1;
					font-weight: 800;
					font-size: 80px;
					text-transform: uppercase;
					@media screen and (max-width: 1600px) {
						font-size: 60px; }
					@media screen and (max-width: 1200px) {
						font-size: 50px; }
					@media screen and (max-width: 900px) {
						font-size: 40px; }
					@media screen and (max-width: 768px) {
						font-size: 50px; }
					@media screen and (max-width: 620px) {
						font-size: 32px; }
					@media screen and (max-width: 440px) {
						font-size: 28px; }
					@media screen and (max-width: 320px) {
						font-size: 25px; } }

				& h3 {
					font-weight: 800;
					font-size: 33px;
					text-transform: uppercase;
					color: $text-color1;
					@media screen and (max-width: 768px) {
						font-size: 23px; }
					@media screen and (max-width: 620px) {
						font-size: 21px; }
					@media screen and (max-width: 440px) {
						font-size: 20px; }
					@media screen and (max-width: 320px) {
						font-size: 19px; } } }

			.myLogo {
				width: 150px;
				height: 150px;
				border-radius: 10px;
				margin: 0 auto 50px;
				background-image: url('./../../assets/img/mp11.jpg');
				box-shadow: 0px 0px 20px 1px rgba(255, 255, 255, .4);
				background-size: cover;
				background-position: center;
				background-repeat: no-repeat; } } } }


