@import '../../common/variables';

.menu {
	display: flex;
	justify-content: center;
	margin-bottom: 50px;
	@media screen and (max-width: 565px) {
		margin-bottom: 30px; }
	@media screen and (max-width: 745px) {
		margin-bottom: 10px; }

	& ul {
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		list-style-type: none;
		width: 100%;
		margin: 10px 0;
		padding: 0;

		& li {
			display: flex;
			justify-content: center;
			width: 100px;
			margin: 0 5px;
			padding: 10px 0;
			cursor: pointer;
			background-color: #f2f2f2;
			border: 1px solid lightgrey;
			border-radius: 5px;
			color: #6f6f6f;
			font-size: 12px;
			font-weight: 400;
			text-transform: uppercase;
			@media screen and (max-width: 860px) {
				margin: 0 3px; }
			@media screen and (max-width: 645px) {
				width: 90%;
				margin: 5px 0; }

			&.active {
				background-color: $main-color;
				color: $text-color1; }

			&:hover {
				background: #d14734 linear-gradient(to bottom, #dd4c38 5%, $main-color 100%);
				color: $text-color1; } } } }

//WorkBlock
.portfolioContainer {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	@media screen and (max-width: 870px) {
		width: 100%; }

	.work {
		width: 360px;
		height: 280px;
		margin: 15px;
		position: relative;
		background-attachment: scroll !important;
		background-position: center center;
		background-size: cover;
		background-repeat: no-repeat;
		transition: .5s ease-in-out;
		border: 1px solid lightgrey;
		@media screen and (max-width: 870px) {
			width: 310px;
			height: 250px;
			margin: 10px;
			padding: 20px; }
		@media screen and (max-width: 670px) {
			width: 275px;
			height: 250px;
			margin: 10px; }
		@media screen and (max-width: 594px) {
			margin: 3px; }
		@media screen and (max-width: 565px) {
			width: 90%;
			height: 250px; }

		.curtain {
			position: absolute;
			content: '';
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;
			z-index: 0;
			transition: all 0.3s ease-in-out;

			&:hover {
				background-color: rgba(245, 79, 54, 0.9);

				& .details {
					display: flex; } } }

		.details {
			display: none;
			position: relative;
			z-index: 1;
			color: #FFFFFF;
			font-size: 16px;
			flex-direction: column;
			justify-content: space-between;

			width: 100%;
			height: 100%;
			padding: 30px 20px;
			@media screen and (max-width: 870px) {
				padding: 20px; }
			@media screen and (max-width: 565px) {
				padding: 30px 20px; }
			@media screen and (max-width: 360px) {
				padding: 20px; }

			.github, .demo {
				font-size: 13px;
				font-weight: 600;

				& span {
					padding: 0 5px 0 0; } }

			& a {
				font-size: 10px;
				color: $text-color1;
				text-decoration: none;

				&:hover {
					text-decoration: underline; } }

			& h2 {
				font-weight: 700;
				line-height: 1.3;
				padding-bottom: 10px;
				border-bottom: 1px solid white;
				@media screen and (max-width: 870px) {
					font-size: 18px;
					line-height: 1.2; } }

			& p {
				margin: 15px 0;
				font-size: 14px;
				border-bottom: 1px solid white;
				padding-bottom: 10px;
				@media screen and (max-width: 670px) {
					margin: 10px 0;
					font-size: 13px; }
				@media screen and (max-width: 565px) {
					margin: 15px 0;
					font-size: 14px; }
				@media screen and (max-width: 360px) {
					margin: 10px 0;
					font-size: 13px; }

				& span {
					text-transform: uppercase; } } } } }
