@import '../../../common/variables';
.lang {
	display: flex;
	position: absolute;
	z-index: 11;
	right: 20px;
	top: 20px;
	@media screen and (max-width: 565px) {
		flex-direction: column;
		right: 0;
		top: 0; }

	& button {
		font-size: 16px;
		font-weight: 900;
		background: $main-color;
		border-radius: 5px;
		padding: 5px 12px;
		margin: 1px;
		color: $text-color1;
		display: inline-block;
		text-align: center;
		text-transform: uppercase;
		border: none;
		cursor: pointer;
		@media screen and (max-width: 565px) {
			font-size: 12px;
			padding: 8px 15px;
			margin: .5px;
			border-radius: 5px 0 0 5px; }

		&:active, &:focus {
			outline: none; }

		&:hover {
			background: #d14734 linear-gradient(to bottom, #dd4c38 5%, $main-color 100%); }

		&:active {
			position: relative;
			top: 1px; } } }

