@import '../../common/variables';

.commonInf {
	display: flex;
	width: 100%;

	h2 {
		padding: 0 0 20px 0; }

	@media screen and (max-width: 1025px) {
		width: 100%; }
	@media screen and (max-width: 950px) {
		flex-direction: column; }

	.left {
		width: 40%;
		display: flex;
		flex-direction: column;
		align-items: center;
		@media screen and (max-width: 950px) {
			width: 100%; } }

	.right {
		width: 60%;
		display: flex;
		flex-direction: column;
		align-items: center;
		position: relative;
		@media screen and (max-width: 950px) {
			width: 100%; }

		.contactForm {
			display: flex;
			flex-direction: column;
			width: 90%;
			margin: 0 auto;
			color: $title-h2-color;
			@media screen and (max-width: 950px) {
				width: 60%; }
			@media screen and (max-width: 700px) {
				width: 90%; }

			& label span {
				font-weight: 600; }

			& input, & textarea {
				font-size: 16px;
				height: 64px;
				padding: 26px 3%;
				background: none repeat scroll 0 0 #f2f2f2;
				border-radius: 3px;
				margin-bottom: 25px;
				width: 100%;
				border: 0;
				@media screen and (min-width: 768px) {
					font-size: 14px; } }

			& textarea {
				height: 200px;
				resize: none;
				font-weight: 400; }

			& input::placeholder, & textarea::placeholder {
				color: #b5c2cf;
				opacity: 1;
				font-weight: 600; }

			.button {
				text-align: center; }

			.error {
				color: $main-color;
				margin-top: -27px;
				padding-bottom: 9px;
				font-size: 12px;
				font-weight: 600;
				text-decoration: underline; } }

		.sentPlace {
			width: 100%;
			height: 105%;
			margin: 0;
			background-color: rgba(242, 242, 242, 0.7);
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			text-align: center;
			position: absolute;
			top: -15px;
			left: 0;

			& span {
				font-size: 32px;
				color: $main-color;
				font-weight: 700;
				background-color: #fff;
				display: inline-block;
				width: 100%;
				padding: 25px;
				@media screen and (max-width: 450px) {
					font-size: 25px; }
				@media screen and (max-width: 450px) {
					font-size: 20px; } } } } }

